*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-text-color: #fcfcfc;
  --secondary-black-text-color: #131313;
  --secondary-grey-text-color: #ababab;
  --primary-title-color: #000;
  --card-green-color: #aec6a5;
  --card-brown-color: #d6936d;
  --card-yellow-color: #efd478;
  --card-background-color: #fff;
  --section-color: #84a178;
  --lines-color: #e1e1e1;
  --primary-btn-color: #f8d24e;
  --secondary-btn-color: #0a8817;
  --time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  background-color: var(--section-color);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.button {
  color: var(--primary-title-color);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 12px 35px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
  display: inline-flex;
}

.button__svg {
  width: 7px;
  height: 11px;
  fill: none;
  stroke: currentColor;
  margin-left: 16px;
}

.button_fill {
  background-color: var(--primary-btn-color);
  transition: background-color 250ms var(--time-function);
}

.button_fill:hover, .button_fill:focus, .button_fill:active {
  background-color: var(--secondary-btn-color);
}

.button_transparent {
  border: 2px solid var(--primary-btn-color);
  transition: border-color 250ms var(--time-function);
  background-color: rgba(0, 0, 0, 0);
  padding: 10px 33px;
}

.button_transparent:hover, .button_transparent:focus, .button_transparent:active {
  border-color: var(--secondary-btn-color);
}

.button_color-light {
  color: var(--primary-text-color);
}

