.mobile-menu__open-btn {
  height: 32px;
  width: 32px;
  fill: var(--primary-text-color);
  transition: fill 250ms var(--time-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding-top: 6px;
  padding-bottom: 6px;
}

.mobile-menu__open-btn:hover, .mobile-menu__open-btn:focus {
