.gallery {
  min-height: 24px;
  background-repeat: no-repeat;
  position: relative;
}

.gallery-container {
  padding: 0 20px;
}

.gallery-photo-container {
  position: relative;
}

@keyframes changePhotoGallery {
  0% {
    opacity: 1;
  }

  17% {
    opacity: 1;
  }

  25% {
    opacity: 0;
  }

  92% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.gallery-photo {
  animation-name: changePhotoGallery;
  animation-duration: 8s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.gallery-photo:nth-of-type(1) {
  animation-delay: 6s;
}

.gallery-photo:nth-of-type(2) {
  animation-delay: 4s;
